<template>
  <div class="edit container">
      <div class="col-md-8 m-auto">
      <router-link to="/staff" class="btn btn-primary btn-sm"></router-link>
  <h1 class="page-header"> </h1>
  <form v-on:submit="updateStaf">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>  </h4>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="staf.last_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="staf.first_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="staf.second_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="staf.exp">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="staf.phone">
            </div>
        </div>
        <button type="" class="btn btn-primary col-md-12"></button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        staf: {},
        alert:''
        }
    },
    methods: {
        fetchStaf(id){
            this.$http.get('http://slimapp/api/staff/'+id)
            .then(function(response){
                this.staf = response.body;
            });
        },
      updateStaf(e){
          if(!this.staf.last_name || !this.staf.first_name || !this.staf.second_name || !this.staf.exp || !this.staf.phone){
                this.alert = ',    ';
            } else {
                let updStaf = {
                    last_name: this.staf.last_name,
                    first_name: this.staf.first_name,
                    second_name: this.staf.second_name,
                    exp: this.staf.exp,
                    phone: this.staf.phone
                }
                this.$http.put('http://slimapp/api/staff/update/'+this.$route.params.id, updStaf)
                    .then(function(response){
                        this.$router.push({path: '/staff', query: {alert: ' '}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchStaf(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

