<template>
  <div class="edit container">
      <div class="col-md-8 m-auto">
       <router-link to="/repair log" class="btn btn-primary btn-sm"></router-link>
  <h1 class="page-header">  </h1>
  <form v-on:submit="updateReplog">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>   </h4>
            <div class="form-group mb-3 col-md-auto">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="replog.id_registry">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="replog.id_employee">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="replog.Date">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="replog.Time">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="replog.status">
            </div>
        </div>
        <button type="" class="btn btn-primary col-md-12"></button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        replog: {},
        alert:''
        }
    },
    methods: {
        fetchReplog(id){
            this.$http.get('http://slimapp/api/repair_log/'+id)
            .then(function(response){
                this.replog = response.body;
            });
        },
      updateReplog(e){
          if(!this.replog.id_registry || !this.replog.id_employee || !this.replog.Date || !this.replog.Time || !this.replog.status){
                this.alert = ',    ';
            } else {
                let updReplog = {
                    id_registry: this.replog.id_registry,
                    id_employee: this.replog.id_employee,
                    Date: this.replog.Date,
                    Time: this.replog.Time,
                    status: this.replog.status
                }
                this.$http.put('http://slimapp/api/repair_log/update/'+this.$route.params.id, updReplog)
                    .then(function(response){
                        this.$router.push({path: '/repair log', query: {alert: '  '}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchReplog(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

